import React from 'react';
import { Panel, PanelHeader, Group, Div, Button, List, Avatar, ListItem, Tabs, TabsItem, FixedLayout, HorizontalScroll, InfoRow, Cell} from '@vkontakte/vkui';
import fetchJsonp from 'fetch-jsonp';

import './../common.css';


class Home extends React.Component {

    constructor(props) {
        super(props)
        this.state={
            money: [],
            cab_id: 0,
            activeTab: 0
        }

        this.getItems = this.getItems.bind(this)
    }

    getItems() {
        let api = `https://activeusers.ru/miniapi.php?mode=user&` + this.props.t.signParams();
        fetchJsonp(api)
            .then(res => res.json())
            .then(data => this.setState({ money : data.data.money, cab_id: data.data.cab_id}))
            .catch(e => [])
    }

    componentDidMount () {
        this.getItems();
        console.log(window.location);
    }

    render() {
        let props = this.props;
        return (
        <Panel id={props.id}>
            <PanelHeader>ActiveUsers</PanelHeader>

            {props.fetchedUser &&
            <Group title="">
                <ListItem
                    before={props.fetchedUser.photo_200 ? <Avatar src={props.fetchedUser.photo_200}/> : null}
                    description={props.fetchedUser.city && props.fetchedUser.city.title ? props.fetchedUser.city.title : ''}
                    >
                    {`${props.fetchedUser.first_name} ${props.fetchedUser.last_name}`}
                </ListItem>
            </Group>}

            <Tabs>
                {this.state.money.map((e, index) => (
                <TabsItem
                    onClick={() => this.setState({activeTab : index})}
                    selected={this.state.activeTab === index}
                >
                    {e.n}
                </TabsItem>
                ))
                }
            </Tabs>

            {this.state.money.map((e, index) => {
                if(this.state.activeTab != index) return false;
                return (
            <List>
                {
                    e.i.length > 0 && e.i.map((item, index2) => {
                        if (item.c == 0) return false;
                        let icon = 'https://activeusers.ru/upload/cab/' + this.state.cab_id + '/' + item.m;
                        return (
                            <Cell key={index2}
                                  before={
                                    <img
                                        alt=""
                                        style={{
                                            width: 40,
                                            height : 40,
                                            margin : 10
                                        }}
                                        src={icon}
                                        />
                                    }
                                  indicator={item.c}
                                  multiline>
                                {item.t}
                            </Cell>
                        )
                    })
                }
            </List>
            )})}
        </Panel>
        );
    }
}
export default Home;